.App {
  height: 100vh;
  padding-top: 4px;
  background: linear-gradient(to right, #4b2a93,#743b97 ,#661a6b,#3a0a3a );
  font-family:  Lato,sans-serif;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

}
.freecodecamp-logo-contenedor {
  width: 100%;
  height: 20px;
  margin: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  
}

.freecode-camp-logo {
  width: 280px;
  height: 65px;
  border-radius: 20px;
  border: 3px solid #0c0c0a;
  margin-bottom: 3px;
}

.contenedor-calculadora {
  width: 350px;
  height:560px ;
  padding: 10px;
  background-color:rgb(102, 180, 154);
  border-radius: 20px;
  box-shadow: 25px 25px 75px  rgba(0, 0, 0, 0.536),
  inset 5px 5px 10px rgba(0, 0, 0, 0.5),
  inset 5px 5px 20px rgba(255, 255, 255, 0.341),
  inset -5px -5px 15px rgba(0, 0, 0, 0.75);
}
.fila {
  margin: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  
}
