.boton-clear{
  height: 80px;
  font-size: 1.2em;
  display: flex;
  flex: 1;
  background-color: rgb(222, 174, 142);
  margin-top: 8px;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: rgb(23, 23, 24);
  border: 3px solid white;
  cursor: pointer;
  border-radius: 20px;
  
}
.boton-clear:hover{
  background-image: linear-gradient(to right, #f6d365 20%, #fda085 51%, #f6d365 100%);
}