
.boton-contenedor{
  height: 70px;
  display: flex;
  flex:1 1;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 30px;
  background-color:#9090cf;
  color: rgb(40, 34, 34);
  font-family: cursive;
  border-radius: 25px;
  border: 2px solid white;
  margin: 4px;
  cursor: pointer;
  user-select:none;

}
.operador{
  background-color: rgb(168, 223, 168);
}
.boton-contenedor:hover {
  background-color: #5a01a7;
}
  .operador:hover {
    background-color: rgb(222, 174, 142);
  }