.input {
  height: 65px;
  border-radius: 50px;
  margin-bottom: 15px ;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 30px;
  font-weight: bold;
  font-family: cursive;
  background-color: rgb(227, 187, 160);
  color: rgb(51, 45, 45);
  padding: 11px 30px 11px 11px;
  margin: 1px solid #888;
  box-shadow: -1px 4px 1px 1px rgb(47, 45, 45);
  
}